*{
    box-sizing: border-box;
}


body{
    background-color: #17181f;
    color: #aaa;
    font-family: 'poppins';
   
}

main{
    max-width: 500px;
    margin: 20px auto;
}

form{
    border: 3px solid #303130;
    border-radius: 10px;
    padding: 5px;
    display: flex;
}

form input[type="text"]{
    background: none;
    border: 0;
    padding: 10px 15px;
    display: block;
    width: 100%;
    color: #ffffff;
    font-size: 25px;
    font-family: 'poppins';
}

form button{
    background-color: #ffbc03;
    border: 0;
    border-radius: 6px;
    cursor: pointer;
    width: 55px;
    font-weight: 800;
    font-size: 30px;
}

div.task{
    /* font-weight: 600; */
    color: rgb(255, 255, 255);
    display: flex;
    background-color: #303130;
    border-radius: 10px;
    padding: 5px 9px;
    margin-top: 5px;
    transition: opacity .3s linear;
   font-size: 20px;
   
   align-items: center;
}

.icon{
    height: 20px;
    margin-right: 4px;
    fill: #ffbc03;
   
}

div.task.done{
    opacity: 0.4;
  
   }

   div.task span{
    position: relative;
    
   }

   div.task-name,
   div.task form{
    flex-grow: 1;
   }

   div.task span:before{
    position: absolute;
    top: 15px;
    content: '';
    display: block;
    width: 0%;
    height: 2px;
    background-color: #ddd;
    transition: width .1s linear;
   }

   div.task.done span:before {
    width: 100%;
   }

   h1, h2, h3{
    text-align: center;
    color: white;
    
   }
  

   /*  flex justify-between items-center h-24 text-white w-full max-w-[1024px] mx-auto px-4 top-0 sticky z-20 */

   .footer{
    font-size: 20px;
    font-weight: 400; 
    padding-top: 50px;
   

   }

   h1{
    color: #ffbc03;
    font-weight: bold;
   
   }


   .trash{
    fill: #aaa;
    height: 14px;
    display: flex;
    cursor: pointer;
   }

   div.checkbox{
    display: flex;
    align-items: center;
   }

   div.task input{
    border: 2px solid #555;
    border-radius: 5px;
    padding:  4px;
    color: rgb(255, 255, 255);
   }